<template>
    <div id="app" class="container">
        <h2>Vue 学习笔记</h2>
        <div v-if="$route.path === '/detail'" class="col-md-1 tag">
            <router-link :to="{path: '/'}" class="btn-back">
                <i class="glyphicon glyphicon-arrow-left"></i>&nbsp;&nbsp;返回
            </router-link>
        </div>
        <div :class="{'col-md-11':($route.path === '/detail'),'col-md-12':!($route.path === '/detail')}" class="search">
            <div class="input-group">
                <span class="input-group-addon" id="basic-addon1"><i class="glyphicon glyphicon-search"></i></span>
                <input type="text" class="form-control" placeholder="Search" v-model="searchKey" aria-describedby="basic-addon1">
            </div>
        </div>
        <ul>
            <li v-if="$route.path === '/detail'" class="col-md-12">
                <router-view></router-view>
            </li>
            <li v-if="$route.path !== '/detail'" v-for="item in tempItems" class="col-md-6 tag">
                <router-link :to='{path: "/detail", query: {name: item.router}}' v-html="item.title"></router-link>
            </li>
        </ul>
    </div>
</template>
<script>
import data from "./data.js"
import router from "./router"
export default {
    name: 'app',
    data() {
        return {
            items: data.items,
            searchKey: '',
            redKey: [],
            tempItems: data.items
        }
    },
    watch: {
        searchKey(newVal, oldVal) {
            router.push({
                path: '/'
            })
            if (!this.searchKey || this.searchKey.length <= 0) {
                this.tempItems = this.items
            }

            this.redKey.length = 0;
            this.tempItems = this.items.filter((item) => {
                if (item.title.toUpperCase().includes(this.searchKey.toUpperCase())) {
                    var start = item.title.toUpperCase().indexOf(this.searchKey.toUpperCase())
                    this.redKey.push(item.title.slice(start, start + this.searchKey.length))
                }
                return item.title.toUpperCase().includes(this.searchKey.toUpperCase())
            }).map((item, index) => {
                var vKey = this.searchKey;
                var regExp = new RegExp(vKey, "gim");
                return {
                    "title": item.title.replace(regExp, '<span class="red">' + this.redKey[index] + '</span>'),
                    "router": item.router
                };
            })
        }
    }
}
</script>
<style>
* {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Lato', Calibri, Arial, sans-serif;
    background: #f4f4f4 url(./assets/bg.jpg);
    font-weight: 300;
    font-size: 15px;
    color: #333;
    overflow: scroll;
    overflow-x: hidden;
}

h2 {
    color: #18aaf2;
    padding: 0 15px;
}

.red {
    color: red;
}

ul li {
    display: block;
}

.search div input {
    outline: none;
}

.search div {
    background: #fff;
}

.search div {
    background: #fff;
    color: #5a5a5a;
    cursor: pointer;
    font-weight: 700;
    font-size: 18px;
    padding: 5px 10px;
    -moz-box-shadow: 3px 3px 16px #333333;
    -webkit-box-shadow: 3px 3px 16px #333333;
    box-shadow: 3px 3px 16px #333333;
    -webkit-border-radius: 0.2em;
    border-radius: 0.2em;
}

.tag a {
    background: #fff;
    display: block;
    color: #5a5a5a;
    cursor: pointer;
    font-weight: 700;
    font-size: 18px;
    padding: 10px 20px;
    -moz-box-shadow: 3px 3px 16px #333333;
    -webkit-box-shadow: 3px 3px 16px #333333;
    box-shadow: 3px 3px 16px #333333;
    -webkit-border-radius: 0.2em;
    border-radius: 0.2em;
    text-decoration: none;
}

.tag a:hover {
    background: #18aaf2;
    color: #fff;
}

.search {
    margin-top: 20px;
}

.tag {
    margin-top: 20px;
}

.tag:last-child {
    margin-bottom: 20px;
}

.tag .btn-back {
    width: 80px;
    font-size: 14px;
    padding: 12px 15px;
}
</style>



